@charset 'utf-8';

img {
    max-width: 100%;
    height: auto;
}

header {
    display: none;
}

#sidebar {
    display: none;
}

.background-contain {
    background-size: contain;
}

.background-cover {
    background-size: cover;
}

.intro {
    font-family: 'MonumentExtended',
        'Noto Sans KR',
        sans-serif;
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    height: calc(var(--vh, 1vh) * 100);
    background: var(--background-image, '') center center / cover no-repeat;
    background-color: var(--intro-background-color, #f5f5f5);
}

.intro_wrapper {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: calc(var(--vh, 1vh) * 6.8) 4.8vw;
}

.intro_tit {
    margin-top: auto;
}

.intro_main-tit {
    width: 50%;
    color: var(--intro-main-text-color, #f0f0f0);
    font-size: 80px;
    font-weight: 100;
    line-height: 1;
    text-transform: capitalize;
}

.intro_main-tit>strong {
    font-weight: 400;
}

.intro_sub-tit {
    opacity: 0.8;
    color: var(--intro-sub-text-color, #f0f0f0);
    display: block;
    font-size: 1.1em;
    letter-spacing: 0.3em;
    padding-top: 1em;
}

.intro_bottom::before {
    display: block;
    position: absolute;
    width: 100%;
    height: 1px;
    top: 0;
    background-color: var(--intro-sub-text-color, #f0f0f0);
    content: '';
}

.intro_bottom {
    color: var(--intro-sub-text-color, #f0f0f0);
    position: relative;
    font-weight: 100;
    font-size: 18px;
    display: flex;
    gap: 36px;
    margin-top: auto;
    padding-top: 25px;
}

.intro_option {
    position: relative;
    padding-left: 16px;
}

.intro_option:before {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0px;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    border: 1px solid var(--intro-main-text-color, #555555);
    content: '';
    opacity: 0.7;
}

.intro_card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24vw;
    height: 100%;
    margin-left: auto;
    margin-right: 15vw;
}

.intro_card_item {
    position: absolute;
    border-radius: 10px;
    background: var(--intro-card-item-img) no-repeat center / var(--intro-card-item-size, contain) var(--intro-card-item-color, #707070);
    box-shadow: 4px 11px 16px rgb(0 0 0 / 25%);
}

.article_h2 {
    font-size: 2.85rem;
    font-family: 'MonumentExtended',
        sans-serif;
    font-weight: 100;
}

.detail_article {
    padding: calc(var(--vh, 1vh) * 13.8) 0 calc(var(--vh, 1vh) * 18.5);
}

.detail_container {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    width: 75%;
    margin: 0 auto;
    color: #333333;
}

.detail_h2 {
    width: 50%;
    margin-bottom: 2rem;
}

.detail_p {
    width: 50%;
    word-break: keep-all;
    line-height: 1.25em;
    margin-bottom: 1rem;
}

.detail_tit>*:last-child {
    margin-bottom: 0;
}

.detail_img {
    margin: calc(var(--vh, 1vh) * 8) 0;
}

.example {
    background: var(--example-background-color);
}

.example_h2 {
    color: var(--example-title-color);
    text-align: center;
    margin-bottom: 7vw;
}

.example_img-container {
    --example-gap: 2vw;
    display: flex;
    flex-wrap: wrap;
    gap: 11vh var(--example-gap);
    width: 75%;
    margin: 0 auto;
}

.example_img__width50 {
    width: calc(50% - var(--example-gap) / 2);
}

.example_img__width75 {
    width: calc(75% - var(--example-gap) / 2);
}

.example_img__float-right {
    margin-left: auto;
}


/* for design-circus */
@media (max-width: 1000px) {
    .intro {
        height: 75vw;
    }
}

@media (max-width: 640px) {
    .intro_card {
        width: 50%;
        margin: 0 auto;
    }
}